"use client";

import React, { useState, useEffect } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Progress } from "@/components/ui/progress";
import { useGameState } from "@/lib/gameState";
import { sampleItems } from "@/lib/items";

type Enemy = {
  name: string;
  health: number;
  maxHealth: number;
  damage: number;
  description: string;
};

const enemies = [
  {
    name: "Fallen",
    health: 25,
    maxHealth: 25,
    damage: 8,
    description: "A small demonic creature with sharp claws"
  },
  {
    name: "Zombie",
    health: 40,
    maxHealth: 40,
    damage: 12,
    description: "A shambling undead warrior"
  },
  {
    name: "Skeleton",
    health: 35,
    maxHealth: 35,
    damage: 10,
    description: "An animated pile of bones wielding ancient weapons"
  }
];

export default function CombatArea() {
  const { character, health, updateHealth, addItem } = useGameState();
  const [currentEnemy, setCurrentEnemy] = useState<Enemy | null>(null);
  const [combatLog, setCombatLog] = useState<string[]>([]);
  const [isPlayerTurn, setIsPlayerTurn] = useState(true);
  const [battleResult, setBattleResult] = useState<'victory' | 'defeat' | null>(null);

  const spawnEnemy = () => {
    const randomEnemy = enemies[Math.floor(Math.random() * enemies.length)];
    setCurrentEnemy({ ...randomEnemy });
    setCombatLog([`A wild ${randomEnemy.name} appears!`]);
    setBattleResult(null);
    setIsPlayerTurn(true);
  };

  const playerAttack = () => {
    if (!currentEnemy || !isPlayerTurn || battleResult) return;

    const baseDamage = character?.baseStrength || 10;
    const damage = Math.floor(Math.random() * baseDamage) + Math.floor(baseDamage / 2);
    
    const newEnemyHealth = Math.max(currentEnemy.health - damage, 0);
    setCurrentEnemy(prev => prev ? { ...prev, health: newEnemyHealth } : null);
    
    setCombatLog(prev => [...prev, `You attack for ${damage} damage!`]);
    
    if (newEnemyHealth <= 0) {
      setCombatLog(prev => [...prev, `${currentEnemy.name} is defeated!`]);
      setBattleResult('victory');
      
      // Random item drop
      if (Math.random() < 0.3) {
        const randomItem = sampleItems[Math.floor(Math.random() * sampleItems.length)];
        addItem(randomItem);
        setCombatLog(prev => [...prev, `You found: ${randomItem.name}!`]);
      }
    } else {
      setIsPlayerTurn(false);
      // Enemy attacks after a delay
      setTimeout(enemyAttack, 1000);
    }
  };

  const enemyAttack = () => {
    if (!currentEnemy || battleResult) return;

    const damage = Math.floor(Math.random() * currentEnemy.damage) + Math.floor(currentEnemy.damage / 2);
    const newPlayerHealth = Math.max(health - damage, 0);
    
    updateHealth(newPlayerHealth);
    setCombatLog(prev => [...prev, `${currentEnemy.name} attacks for ${damage} damage!`]);
    
    if (newPlayerHealth <= 0) {
      setCombatLog(prev => [...prev, "You have been defeated!"]);
      setBattleResult('defeat');
    } else {
      setIsPlayerTurn(true);
    }
  };

  const restoreHealth = () => {
    if (character) {
      updateHealth(character.baseHealth);
      setCombatLog(prev => [...prev, "You rest and restore your health."]);
    }
  };

  useEffect(() => {
    if (!currentEnemy) {
      spawnEnemy();
    }
  }, []);

  if (!character) {
    return (
      <Card>
        <CardContent className="p-8 text-center">
          <p>Please select a character first!</p>
        </CardContent>
      </Card>
    );
  }

  return (
    <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
      {/* Combat Arena */}
      <Card>
        <CardHeader>
          <CardTitle>Combat Arena</CardTitle>
          <CardDescription>Face the creatures of darkness</CardDescription>
        </CardHeader>
        <CardContent className="space-y-6">
          {/* Player Status */}
          <div>
            <div className="flex justify-between items-center mb-2">
              <h3 className="font-bold">{character.name}</h3>
              <span className="text-sm">{health}/{character.baseHealth}</span>
            </div>
            <Progress 
              value={(health / character.baseHealth) * 100} 
              className="h-3"
            />
          </div>

          {/* Enemy Status */}
          {currentEnemy && (
            <div>
              <div className="flex justify-between items-center mb-2">
                <h3 className="font-bold text-red-400">{currentEnemy.name}</h3>
                <span className="text-sm">{currentEnemy.health}/{currentEnemy.maxHealth}</span>
              </div>
              <Progress 
                value={(currentEnemy.health / currentEnemy.maxHealth) * 100} 
                className="h-3"
              />
              <p className="text-sm text-muted-foreground mt-2">
                {currentEnemy.description}
              </p>
            </div>
          )}

          {/* Combat Actions */}
          <div className="flex flex-wrap gap-2">
            <Button 
              onClick={playerAttack}
              disabled={!isPlayerTurn || battleResult !== null || health <= 0}
              className="flex-1"
            >
              Attack
            </Button>
            <Button 
              onClick={restoreHealth}
              variant="secondary"
              disabled={health >= (character?.baseHealth || 100)}
            >
              Rest
            </Button>
            <Button 
              onClick={spawnEnemy}
              variant="outline"
              disabled={!battleResult && (currentEnemy?.health ?? 0) > 0}
            >
              New Enemy
            </Button>
          </div>

          {/* Battle Result */}
          {battleResult && (
            <div className={`text-center p-4 rounded-lg ${
              battleResult === 'victory' 
                ? 'bg-green-500/20 text-green-400' 
                : 'bg-red-500/20 text-red-400'
            }`}>
              <h3 className="font-bold text-lg">
                {battleResult === 'victory' ? 'Victory!' : 'Defeat!'}
              </h3>
              <p className="text-sm">
                {battleResult === 'victory' 
                  ? 'You have defeated your enemy!' 
                  : 'You have been defeated. Rest to recover.'}
              </p>
            </div>
          )}
        </CardContent>
      </Card>

      {/* Combat Log */}
      <Card>
        <CardHeader>
          <CardTitle>Combat Log</CardTitle>
          <CardDescription>Battle history and events</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="h-64 overflow-y-auto space-y-1 text-sm">
            {combatLog.map((log, index) => (
              <div key={index} className="p-2 bg-secondary/50 rounded">
                {log}
              </div>
            ))}
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
